import React from 'react'
import styled from 'styled-components'
import { Heading, Text } from '@pancakeswap/uikit'
import Container from 'components/layout/Container'

const Title = styled(Heading).attrs({ as: 'h1', scale: 'xl' })`
  color: #ffffff;
  margin-bottom: 24px;
  text-shadow: 2px 2px 2px #00000040;
`

const Subtitle = styled(Text)`
  background: -webkit-linear-gradient(#ffd800, #eb8c00);
  font-size: 24px;
  font-weight: 600;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
`

const StyledHero = styled.div`
  background-image: linear-gradient(#7645d9, #452a7a);
  max-height: max-content;
  overflow: hidden;
  ${({ theme }) => theme.mediaQueries.sm} {
    max-height: 256px;
  }
`

const StyledContainer = styled(Container)`
  display: flex;

  flex-direction: column-reverse;

  ${({ theme }) => theme.mediaQueries.sm} {
    flex-direction: row;
  }
`

export interface Props {
  title?: string
  subtitle?: string
  text?: string
  rightContent?: JSX.Element
  leftFlex?: number
  rightFlex?: number
  hideRightOnMobile?: boolean
}

const PageHero = ({ title, subtitle, text, rightContent, leftFlex = 1, rightFlex = 1, hideRightOnMobile }: Props) => {
  const LeftWrapper = styled.div`
    flex: ${leftFlex};
    padding-right: 0;
    padding-bottom: 40px;
    padding-top: 40px;

    ${({ theme }) => theme.mediaQueries.sm} {
      padding-right: 24px;
    }

    ${({ theme }) => theme.mediaQueries.lg} {
      padding-right: 32px;
    }
  `

  const RightWrapper = styled.div`
    display: ${hideRightOnMobile ? 'none' : 'flex'};
    align-items: center;
    justify-content: center;
    flex: ${rightFlex};
    padding-left: 0;

    ${({ theme }) => theme.mediaQueries.sm} {
      margin-top: 0;
      padding-left: 24px;
    }

    ${({ theme }) => theme.mediaQueries.lg} {
      display: flex;
      padding-left: 32px;
      & img {
        margin-top: -25px;
      }
    }
  `

  return (
    <StyledHero>
      <StyledContainer>
        <LeftWrapper>
          <Title>{title}</Title>
          <Subtitle>{subtitle}</Subtitle>
          <Text>{text}</Text>
        </LeftWrapper>
        {rightContent && <RightWrapper>{rightContent}</RightWrapper>}
      </StyledContainer>
    </StyledHero>
  )
}

export default PageHero
